import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormGroup from "@material-ui/core/FormGroup";
import Checkbox from "@material-ui/core/Checkbox";
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
const theme = createMuiTheme({
  overrides: {
    // Style sheet name 
    MuiFormControlLabel: {
      // Name of the rule
      label: {
        // Some CSS
        fontSize: "1rem"
      }
    },
    MuiCheckbox: {
      root: {
        top: 0
      }
    }
  }
});

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(2, 1)
  },
  root: {
    display: "flex"
  }
}));

export default function QuestionGroup(props) {
  const classes = useStyles();
  const [value, setValue] = React.useState("");

  const handleChange = (event) => {
    setValue(event.target.value);
    props.handleAnswer(props.Qid, event.target.value);
  };
  const handleCheckChange = (event) => {
    if (event.target.checked) {
      props.handleCheckAnswer(props.Qid, event.target.value, 1);
    } else {
      props.handleCheckAnswer(props.Qid, event.target.value, 0);
    }
  };
  return (
    <ThemeProvider theme={theme}>
      <div>
        <FormControl className={classes.formControl}>
          {props.single === true ? (
            <RadioGroup
              aria-label="Question"
              name="Question"
              value={value}
              onChange={handleChange}
            >
              {props.items.map((item) => (
                <FormControlLabel
                  key={item.id}
                  value={item.id}
                  control={<Radio color="primary" />}
                  label={item.label}
                />
              ))}
            </RadioGroup>
          ) : (
            <div className={classes.root}>
              <FormGroup>
                {props.items.slice(0, props.items.length / 2).map((item) => (
                  <FormControlLabel
                    key={item.id}
                    value={item.id}
                    control={<Checkbox onChange={handleCheckChange} />}
                    label={item.label}
                  />
                ))}
              </FormGroup>
              <FormGroup>
                {props.items
                  .slice(props.items.length / 2, props.items.length)
                  .map((item) => (
                    <FormControlLabel
                      key={item.id}
                      value={item.id}
                      control={<Checkbox onChange={handleCheckChange} />}
                      label={item.label}
                    />
                  ))}
              </FormGroup>
            </div>
          )}
        </FormControl>
      </div>
    </ThemeProvider>
  );
}
