import React, { useState } from "react";
import ReactDOM from "react-dom";
import Para from "./Para";
import {
  createMuiTheme,
  ThemeProvider,
  responsiveFontSizes
} from "@material-ui/core/styles";

let theme = createMuiTheme({
  "@media (min-width: 600px)": {
    typography: {
      fontFamily: [
        '"Segoe UI"',
        "Roboto",
        '"Helvetica Neue"',
        "Arial",
        "-apple-system",
        "BlinkMacSystemFont",
        "sans-serif",
        '"Apple Color Emoji"',
        '"Segoe UI Emoji"',
        '"Segoe UI Symbol"'
      ].join(","),
      h4: { fontSize: "2.5rem", fontWeight: "350" },
      h6: { fontWeight: "350" }
    }
  },
  "@media (max-width: 600px)": {
    typography: {
      fontFamily: [
        '"Segoe UI"',
        "Roboto",
        '"Helvetica Neue"',
        "Arial",
        "-apple-system",
        "BlinkMacSystemFont",
        "sans-serif",
        '"Apple Color Emoji"',
        '"Segoe UI Emoji"',
        '"Segoe UI Symbol"'
      ].join(","),
      h4: { fontSize: "0.5rem", fontWeight: "150" },
      h6: { fontWeight: "350" }
    }
  },
});
theme = responsiveFontSizes(theme);
export default function Page() {
  const [result, setResult] = useState({ Single_Choice: {}, Multi_Choice: {} });
  const [result2, setResult2] = useState({});
  function handleSAnswer(Qid, answer) {
    let resultCopy = JSON.parse(JSON.stringify(result));
    resultCopy["Single_Choice"][Qid] = answer;
    setResult(resultCopy);
    let resultCopy2 = JSON.parse(JSON.stringify(result2));
    resultCopy2[Qid] = answer;
    setResult2(resultCopy2);
  }
  function handleMAnswer(Qid, Cid, answer) {
    let resultCopy = JSON.parse(JSON.stringify(result));
    if (answer === 0) {
      delete resultCopy["Multi_Choice"][Qid][Cid];
    } else if (answer === 1) {
      if (resultCopy["Multi_Choice"][Qid] === undefined)
        resultCopy["Multi_Choice"][Qid] = {};
      resultCopy["Multi_Choice"][Qid][Cid] = true;
    }
    setResult(resultCopy);

    let resultCopy2 = JSON.parse(JSON.stringify(result2));
    if (answer === 0) {
      delete resultCopy2[Qid][Cid];
    } else if (answer === 1) {
      if (resultCopy2[Qid] === undefined)
        resultCopy2[Qid] = {};
      resultCopy2[Qid][Cid] = true;
    }
    setResult2(resultCopy2);
  }
  function handleSubmission() {
    console.log(result);
    console.log(result2);
  }
  return (
    <ThemeProvider theme={theme}>
      <Para
        handleSAnswer={handleSAnswer}
        handleMAnswer={handleMAnswer}
        submission={handleSubmission}
      />
    </ThemeProvider>
  );
}
ReactDOM.render(<Page />, document.querySelector("#root"));
