import React from "react";
import { ParallaxLayer } from "@react-spring/parallax";
const colors = [
  "#efebe3",
  "#ead0d8",
  "#d0dde0",

  "#cee5a0",
  "#edd4d0",
  "#fcfdff",
  "#e5daa0",
  "#a3ccbe",
  "#dde5ce",
  "#e5c9da",
  "#8b9cc9",
  "#b8c4c6"
];
// const colors = [
//   "#ededdf",
//   "#fde3ff",
//   "#63a7cf",
//   "#458c4b",
//   "#fde3ff",
//   "#e6b3aa",
//   "#ebfcff",
//   "#ededdf",
//   "#000000",
//   "#D22631",
//   "#f2c261",
//   "#9fedd3",
//   "#e3afc6"
// ];
export default function ParaBkg(props) {
  const numberOfColors = colors.length;
  const bgImage1 = ["linear-gradient(-180deg,"];
  const bgImage2 = "0%,";
  const bgImage3 = "100%)";
  let bkg = [];
  const creatBkg = () => {
    for (let i = 0; i < props.pages; i++) {
      let bgImage = [];
      let bgImageTransit = [];
      bgImage.push(bgImage1);
      bgImage.push(colors[(i * 2) % numberOfColors]);
      bgImage.push(bgImage2);
      bgImage.push(colors[(i * 2 + 1) % numberOfColors]);
      bgImage.push(bgImage3);
      bgImageTransit.push(bgImage1);
      bgImageTransit.push(colors[(i * 2 + 1) % numberOfColors]);
      bgImageTransit.push(bgImage2);
      bgImageTransit.push(colors[(i * 2 + 2) % numberOfColors]);
      bgImageTransit.push(bgImage3);
      bkg.push(
        <div key={i}>
          <ParallaxLayer
            offset={i}
            speed={1}
            style={{
              backgroundImage: bgImage.join(" ")
            }}
          />
          <ParallaxLayer
            offset={i + 0.999999}
            speed={1}
            style={{
              backgroundImage: bgImageTransit.join(" ")
            }}
          />
        </div>
      );
    }
    return bkg;
  };
  return creatBkg();
}
