import React from "react";
import { ParallaxLayer } from "@react-spring/parallax";
import SurveyGrid from "./SurveyGrid.jsx";
import { ReactComponent as Human1 } from "./assets/art/svg/human/standing-19.svg";
import { ReactComponent as Human2 } from "./assets/art/svg/human/sitting-4.svg";
import { ReactComponent as Human3 } from "./assets/art/svg/human/sitting-7.svg";
import { ReactComponent as Human4 } from "./assets/art/svg/human/standing-18.svg";
import { ReactComponent as Human5 } from "./assets/art/svg/human/standing-2.svg";
import { ReactComponent as Human6 } from "./assets/art/svg/human/sitting-2.svg";
import { ReactComponent as Human7 } from "./assets/art/svg/human/standing-16.svg";
import questionData from "./ParaQuestionsData.jsx";
import { makeStyles } from "@material-ui/core/styles";
const questions = questionData();
const humanStyle = {
    height: "30vh"
};
const useStyles = makeStyles((theme) => ({
  "@media (min-width: 600px)": {
    human: { height: "45vh" }
  },
  "@media (max-width: 600px)": {
    human: { height: "30vh" }
  }
}));

const human = [
  <Human1 style={humanStyle}  />,
  <Human2 style={humanStyle} />,
  <Human3 style={humanStyle} />,
  <Human4 style={humanStyle} />,
  <Human5 style={humanStyle} />,
  <Human6 style={humanStyle} />,
  <Human7 style={humanStyle} />
];
export default function ParaQuestions(props) {
  const classes = useStyles();
  return questions.map((q) => (
    <ParallaxLayer key={q.title} offset={q.id} speed={1}>
      <SurveyGrid
        Q={q["Q"]}
        A={q["A"]}
        id={q["id"]}
        handleAnswer={props.handleSAnswer}
        handleCheckAnswer={props.handleMAnswer}
        scroll={props.scroll}
        offset={q.id}
        single={q.single}
        submission={props.submission}
        final={questions.length}
      >
        {q.id < human.length ? human[q.id] : human[human.length - 1] }
      </SurveyGrid>
    </ParallaxLayer>
  ));
}
