import React from "react";
import { ReactComponent as RippleImg1 } from "./assets/art/svg/ripple4.svg";
import { ReactComponent as RippleImg2 } from "./assets/art/svg/ripple5.svg";
import { makeStyles } from "@material-ui/core/styles";
import { ParallaxLayer } from "@react-spring/parallax";
export default function Ripples(props) {
  const useStyles = makeStyles((theme) => ({
    "@media (min-width: 600px)": {
      container1: {
        position: "absolute",
        left: "55%",
        transform: "translateX(-50%) translateY(15%)",
        marigin: "0 auto"
      },
      container2: {
        position: "absolute",
        left: "30%",
        transform: "translateX(-50%)",
        marigin: "0 auto"
      },
      r1: {
        height: "120vh"
      },      r2: {
        height: "100vh"
      },
    },
    "@media (max-width: 600px)": {
      container1: {
        position: "absolute",
        left: "65%",
        transform: "translateX(-50%)",
        marigin: "0 auto"
      },
      container2: {
        position: "absolute",
        left: "75%",
        transform: "translateX(-50%) translateY(15%)",
        marigin: "0 auto"
      },
      r1: {
        height: "55vh"
      },      r2: {
        height: "60vh"
      },

    }
  }));
  const classes = useStyles();
  return (
    <div>
      <ParallaxLayer offset={0} speed={1.7}>
        <div className={classes.container1}>
          <RippleImg1 className={classes.r1} />
        </div>
      </ParallaxLayer>
      <ParallaxLayer offset={0} speed={2.3}>
        <div className={classes.container2}>
          <RippleImg2 className={classes.r2} />
        </div>
      </ParallaxLayer>
    </div>
  );
}
