import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import QuestionGroup from "./Question.jsx";
import Button from "@material-ui/core/Button";

import Card from "./Card.jsx";

const useStyles = makeStyles((theme) => ({
  "@media (min-width: 600px)": {
    container: {
      width: "100%",
      height: "100%",
      margin: "auto auto",
      display: "flex",
      // marginTop: theme.spacing(1),
      alignItems: "center",
      justifyContent: "center"
    },
    paper: {
      maxWidth: "40vw",
      padding: theme.spacing(2, 2)
    },
    human: { height: "45vh" }
  },
  "@media (max-width: 600px)": {
    container: {
      width: "100%",
      height: "100%",
      margin: "auto auto",
      display: "flex",
      marginTop: theme.spacing(1),
      // alignItems: "center",
      justifyContent: "center"
    },
    paper: {
      maxWidth: "85vw",
      padding: theme.spacing(2, 2)
    },
    human: { height: "30vh" }
  },
  centre: {
    margin: "auto"
  },
  button: {
    marginTop: theme.spacing(0),
    marginRight: theme.spacing(1),
    marginBotton: theme.spacing(1)
  }
}));

export default function SurveyGrid(props) {
  const classes = useStyles();

  return (
    <div className={classes.container}>
      <Grid container spacing={1}>
        <Grid item xs={12} sm={6}>
          <div>
            <div className={classes.container}>
              <div >{props.children}</div>
            </div>
          </div>
        </Grid>
        <Grid item xs={12} sm={6}>
          <div>
            <Card>
              <div className={classes.container}>
                <Paper className={classes.paper}>
                  <Typography variant="h4">{props.Q}</Typography>
                  <QuestionGroup
                    items={props.A}
                    Qid={props.id}
                    single={props.single}
                    handleAnswer={props.handleAnswer}
                    handleCheckAnswer={props.handleCheckAnswer}
                  />
                  <div>
                    <Button
                      className={classes.button}
                      size="large"
                      onClick={() => {
                        props.scroll(parseInt(props.offset) - 1);
                      }}
                      variant="outlined"
                    >
                      <Typography variant="subtitle1">Previous</Typography>
                    </Button>
                    {props.id === props.final - 1 ? (
                      <Button
                        className={classes.button}
                        size="large"
                        onClick={() => {
                          props.submission();
                        }}
                        variant="outlined"
                      >
                        <Typography variant="subtitle1">Submit</Typography>
                      </Button>
                    ) : (
                      <Button
                        className={classes.button}
                        size="large"
                        onClick={() => {
                          props.scroll(parseInt(props.offset) + 1);
                        }}
                        variant="outlined"
                      >
                        <Typography variant="subtitle1">Next</Typography>
                      </Button>
                    )}
                  </div>
                </Paper>
              </div>
            </Card>
          </div>
        </Grid>
      </Grid>
    </div>
  );
}
