import React from "react";
import { ReactComponent as FImg1 } from "./assets/art/svg/Australian_Aboriginal_Flag.svg";
import { ReactComponent as FImg2 } from "./assets/art/svg/Flag_of_the_Torres_Strait_Islanders.svg";
import { ReactComponent as FImg3 } from "./assets/art/svg/Intersex_Pride_Flag.svg";
import { makeStyles } from "@material-ui/core/styles";
import { ParallaxLayer } from "@react-spring/parallax";
export default function Ripples(props) {
  const useStyles = makeStyles((theme) => ({
    "@media (min-width: 600px)": {
      container1: {
        position: "absolute",
        left: "10%",
        top: "20%",
        transform: "translateX(-50%) translateY(-50%)",
        marigin: "0 auto"
      },
      container2: {
        position: "absolute",
        left: "30.5%",
        top: "20%",
        transform: "translateX(-50%) translateY(-50%)",
        marigin: "0 auto"
      },
      container3: {
        position: "absolute",
        left: "50%",
        top: "20%",
        transform: "translateX(-50%) translateY(-50%)",
        marigin: "0 auto"
      },
      r1: {
        height: "20vh"
      },
      r2: {
        height: "20vh"
      },
      r3: {
        height: "20vh"
      }
    },
    "@media (max-width: 600px)": {
      container1: {
        position: "absolute",
        left: "80%",
        top: "10%",
        transform: "translateX(-50%) translateY(-50%)",
        marigin: "0 auto"
      },
      container2: {
        position: "absolute",
        left: "30%",
        top: "30%",
        transform: "translateX(-50%) translateY(-50%)",
        marigin: "0 auto"
      },
      container3: {
        position: "absolute",
        left: "70%",
        top: "50%",
        transform: "translateX(-50%) translateY(-50%)",
        marigin: "0 auto"
      },
      r1: {
        width: "50vw"
      },
      r2: {
        width: "50vw"
      },
      r3: {
        width: "50vw"
      }
    }
  }));
  const classes = useStyles();
  return (
    <div>
      <ParallaxLayer offset={4} speed={1.7}>
        <div className={classes.container1}>
          <FImg1 className={classes.r1} />
        </div>
      </ParallaxLayer>
      <ParallaxLayer offset={4} speed={2.3}>
        <div className={classes.container2}>
          <FImg2 className={classes.r2} />
        </div>
      </ParallaxLayer>
      <ParallaxLayer offset={4} speed={2.9}>
        <div className={classes.container3}>
          <FImg3 className={classes.r3} />
        </div>
      </ParallaxLayer>
    </div>
  );
}
