import React from "react";
import Maproad from "./Map_road.jsx";
import Home from "./Home.jsx";
import Ripples from "./Ripples.jsx";
import ParaQuestions from "./ParaQuestions.jsx";
import Rainbow from "./Rainbow.jsx";
import Bkg from "./ParaBkg.jsx";
import Pop from "./Pop.jsx";
import Flag from "./Flag.jsx";
import { Parallax, ParallaxLayer } from "@react-spring/parallax";

export default class Para extends React.Component {
  constructor(props) {
    super(props);
    this.switch = this.props.switch;
  }
  scroll = (pageNumber) => {
    this.parallax.scrollTo(pageNumber);
  };
  render() {
    return (
      <Parallax ref={(ref) => (this.parallax = ref)} pages={6.5}>
        <Bkg pages={7} />
        <ParallaxLayer offset={1} speed={2}>
          <Maproad height={window.screen.height} />
        </ParallaxLayer>
        <ParallaxLayer offset={2} speed={-2.7}>
          <Home />
        </ParallaxLayer>
        <Flag />
        <Pop />
        <Ripples />
        <Rainbow offset={3} />
        <ParaQuestions
          handleSAnswer={this.props.handleSAnswer}
          handleMAnswer={this.props.handleMAnswer}
          scroll={this.scroll}
          submission={this.props.submission}
        />
      </Parallax>
    );
  }
}
