import React from "react";
import { ParallaxLayer } from "@react-spring/parallax";
const rainbow = [
  {
    id: "rainbow0",
    speed: 7,
    x: "0",
    y: "130",
    height: "1918",
    fill: "#61bb46"
  },
  {
    id: "rainbow1",
    speed: 6,
    x: "250",
    y: "310",
    height: "1838",
    fill: "#fdb827"
  },
  {
    id: "rainbow2",
    speed: 5,
    x: "500",
    y: "450",
    height: "1758",
    fill: "#f5821f"
  },
  {
    id: "rainbow3",
    speed: 4,
    x: "750",
    y: "550",
    height: "1778",
    fill: "#e03a3e"
  },
  {
    id: "rainbow4",
    speed: 3,
    x: "1000",
    y: "610",
    height: "1598",
    fill: "#963d97"
  },
  {
    id: "rainbow5",
    speed: 2,
    x: "1250",
    y: "630",
    height: "1518",
    fill: "#009ddc"
  }
];

export default function Rainbow(props) {
  return rainbow.map((arch) => (
    <ParallaxLayer key={arch.id} offset={props.offset} speed={arch.speed}>
      <svg
        id="sketchpad"
        preserveAspectRatio="xMinYMin meet"
        width="100%"
        viewBox="0 0 1500 3248"
      >
        <rect
          x={arch.x}
          y={arch.y}
          height={arch.height}
          width="250"
          rx="125"
          ry="125"
          fill={arch.fill}
        />
      </svg>
    </ParallaxLayer>
  ));
}
